<template>
  <div id="swagger-ui"></div>
</template>

<script>
var SwaggerUI = require("swagger-ui");

export default {
  data: () => ({
    info: "",
  }),
  async created() {
    // Begin Swagger UI call region
    // eslint-disable-next-line no-unused-vars
    SwaggerUI({
      // url: "../assets/swagger.json",
      specs: {
        openapi: "3.0.0",
        info: {
          title: "Simple Inventory API",
          description: "This is a simple API",
          contact: {
            email: "allen.wenzl@parker.com",
          },
          license: {
            name: "Apache 2.0",
            url: "http://www.apache.org/licenses/LICENSE-2.0.html",
          },
          version: "1.0.0",
        },
        servers: [
          {
            url: "https://virtserver.swaggerhub.com/P6975/test/1.0.0",
            description: "SwaggerHub API Auto Mocking",
          },
          {
            url:
              "https://virtserver.swaggerhub.com/Par13/PistachioDashboard/1.0.0",
            description: "SwaggerHub API Auto Mocking",
          },
        ],
        tags: [
          {
            name: "public",
            description: "API calls used by webbrowser",
          },
          {
            name: "private",
            description: "API calls used by routes",
          },
          {
            name: "button",
            description: "API calls used by buttons",
          },
        ],
        paths: {
          "/": {
            get: {
              tags: ["public"],
              summary: "Loads the default page",
              description: "Load the default page",
              operationId: "DefaultPage",
              responses: {
                "200": {
                  description: "page loaded",
                },
              },
            },
          },
          "/show_image": {
            get: {
              tags: ["public"],
              summary: "Shows the latest image",
              description: "Loads the image to be sorted",
              operationId: "ShowImage",
              responses: {
                "200": {
                  description: "page loaded",
                },
              },
            },
            post: {
              tags: ["public"],
              summary: "Uploads an image",
              description: "Uploads an image",
              operationId: "UploadImage",
              requestBody: {
                description: "Photo Uploaded",
                content: {
                  "application/json": {
                    schema: {
                      $ref: "#/components/schemas/UploadImage",
                    },
                  },
                },
              },
              responses: {
                "200": {
                  description: "item created",
                },
              },
            },
          },
          "/settings": {
            get: {
              tags: ["public"],
              summary: "Loads software settings page",
              description: "Loads software settings page",
              operationId: "SoftwareSettings",
              responses: {
                "200": {
                  description: "page loaded",
                },
              },
            },
          },
          "/camera_settings": {
            get: {
              tags: ["public"],
              summary: "Loads Camera settings page",
              description: "Loads Camera settings page",
              operationId: "CameraSettings",
              responses: {
                "200": {
                  description: "page loaded",
                },
              },
            },
          },
          "/dashboard_settings": {
            get: {
              tags: ["public"],
              summary: "Loads Dashboard settings page",
              description: "Loads Dashboard settings page",
              operationId: "DashboardSettings",
              responses: {
                "200": {
                  description: "item created",
                },
              },
            },
          },
          "/settings_list": {
            get: {
              tags: ["public"],
              summary: "Loads list of settings",
              description: "Loads list of settings",
              operationId: "SettingsList",
              responses: {
                "200": {
                  description: "item created",
                },
              },
            },
          },
          "/_software_settings_file": {
            get: {
              tags: ["private"],
              summary: "Loads software settings file data",
              description: "Loads software settings file data",
              operationId: "SoftwareSettingsFile",
              responses: {
                "200": {
                  description: "json settings data returned",
                  content: {
                    "application/json": {
                      schema: {
                        $ref: "#/components/schemas/SoftwareSettingsFile",
                      },
                    },
                  },
                },
              },
            },
          },
          "/_camera_settings_file": {
            get: {
              tags: ["private"],
              summary: "Loads Camera settings file data",
              description: "Loads Camera settings file data",
              operationId: "CameraSettingsFile",
              responses: {
                "200": {
                  description: "json settings data returned",
                  content: {
                    "application/json": {
                      schema: {
                        $ref: "#/components/schemas/CameraSettingsFile",
                      },
                    },
                  },
                },
              },
            },
          },
          "/_dashboard_settings_file": {
            get: {
              tags: ["private"],
              summary: "Loads Dashboard settings file data",
              description: "Loads Dashboard settings file data",
              operationId: "DashboardSettingsFile",
              responses: {
                "200": {
                  description: "json settings data returned",
                  content: {
                    "application/json": {
                      schema: {
                        $ref: "#/components/schemas/DashboardSettingsFile",
                      },
                    },
                  },
                },
              },
            },
          },
          "/_hose_series": {
            get: {
              tags: ["private"],
              summary: "Gets current hose series",
              description: "Gets current hose series",
              operationId: "HoseSeries",
              responses: {
                "200": {
                  description: "hose series",
                },
              },
            },
          },
          "/_picture_number": {
            get: {
              tags: ["private"],
              summary: "Gets current number of pictures to sort",
              description: "Gets current number of pictures to sort",
              operationId: "PicNumber",
              responses: {
                "200": {
                  description: "Number of Pictures",
                },
              },
            },
          },
          "/admin": {
            get: {
              tags: ["public"],
              summary: "Loads the administration page",
              description: "Load the administration page",
              operationId: "AdminPage",
              responses: {
                "200": {
                  description: "page loaded",
                },
              },
            },
          },
          "/script": {
            get: {
              tags: ["public"],
              summary: "Loads the script page",
              description: "Load the script page",
              operationId: "ScriptPage",
              responses: {
                "200": {
                  description: "page loaded",
                },
              },
            },
          },
          "/_good_button_click": {
            post: {
              tags: ["button"],
              summary: "Moves photo to good folder",
              description: "Classifies a photo as good",
              operationId: "GoodButton",
              requestBody: {
                content: {
                  "text/plain": {
                    schema: {
                      type: "string",
                      example: "4F,801-8",
                    },
                  },
                },
                required: true,
              },
              responses: {
                "200": {
                  description: "photo moved to good folder",
                },
              },
            },
          },
          "/_bad_button_click": {
            post: {
              tags: ["button"],
              summary: "Moves photo to defect folder",
              description: "Classifies a photo as a defect",
              operationId: "BadButton",
              requestBody: {
                content: {
                  "text/plain": {
                    schema: {
                      type: "string",
                      example: "4F,801-8",
                    },
                  },
                },
                required: true,
              },
              responses: {
                "200": {
                  description: "photo moved to defect folder",
                },
              },
            },
          },
          "/_save_settings_file": {
            post: {
              tags: ["button"],
              summary: "Saves data to settings file",
              description:
                "Saves data to settings file (Can be used by all settings files).",
              operationId: "SaveSettingsButton",
              parameters: [
                {
                  name: "file",
                  in: "query",
                  required: true,
                  style: "form",
                  explode: true,
                  schema: {
                    type: "string",
                    example: "software_settings.json",
                  },
                },
              ],
              requestBody: {
                content: {
                  "application/json": {
                    schema: {
                      $ref: "#/components/schemas/DashboardSettingsFile",
                    },
                  },
                },
                required: true,
              },
              responses: {
                "200": {
                  description: "successfully wrote settings file",
                },
              },
            },
          },
          "/_series_button_click": {
            post: {
              tags: ["button"],
              summary: "Changes hose series",
              description: "Changes hose series for defect classification",
              operationId: "SeriesButton",
              requestBody: {
                content: {
                  "text/plain": {
                    schema: {
                      type: "string",
                      example: "801-8",
                    },
                  },
                },
                required: true,
              },
              responses: {
                "200": {
                  description: "hose series changed",
                },
              },
            },
          },
          "/_start_script": {
            post: {
              tags: ["button"],
              summary: "Starts vision system script",
              description: "Starts vision system script",
              operationId: "ScriptStart",
              responses: {
                "200": {
                  description: "script started",
                },
              },
            },
          },
          "/_stop_script": {
            post: {
              tags: ["button"],
              summary: "Stops vision system script",
              description: "Stops vision system script",
              operationId: "ScriptStop",
              responses: {
                "200": {
                  description: "script stopped",
                },
              },
            },
          },
        },
        components: {
          schemas: {
            UploadImage: {
              required: ["photo"],
              type: "object",
              properties: {
                photo: {
                  type: "string",
                  example: "<<bytestring of image data>>",
                },
              },
            },
            SoftwareSettingsFile: {
              required: [
                "API_URL",
                "DEBUG_MODE",
                "HEARTBEAT_ADDRESS",
                "HEARTBEAT_IP",
                "HEARTBEAT_MSG",
                "HEARTBEAT_PULSE",
                "HEARTBEAT_SERVER_PORT",
                "IMAGE_PATH",
                "MODEL_SIZE",
                "PRED_BATCH_SIZE",
                "PRED_THRESHOLD",
                "READER_ADDRESS",
                "READER_END_CHAR",
                "READER_IP",
                "READER_LENGTH",
                "READER_PORT",
                "READER_UPDATE",
                "WEIGHT_PATH",
                "WRITER_ADDRESS",
                "WRITER_IP",
                "WRITER_SERVER_PORT",
                "WRITER_TIMEOUT",
                "_HEARTBEAT_USE",
                "_IMAGE_USE",
                "_READER_USE",
                "_WRITER_USE",
              ],
              type: "object",
              properties: {
                WRITER_IP: {
                  type: "string",
                  example: "169.254.0.1",
                },
                WRITER_SERVER_PORT: {
                  type: "string",
                  example: "502",
                },
                WRITER_ADDRESS: {
                  type: "string",
                  example: "2",
                },
                WRITER_TIMEOUT: {
                  type: "string",
                  example: "0.5",
                },
                _WRITER_USE: {
                  type: "string",
                  example: "true",
                },
                WEIGHT_PATH: {
                  type: "string",
                  example: "Pistachio_Model.pth",
                },
                API_URL: {
                  type: "string",
                  example: "http://0.0.0.0:5000/",
                },
                PRED_THRESHOLD: {
                  type: "string",
                  example: "0.5",
                },
                MODEL_SIZE: {
                  type: "string",
                  example: "34",
                },
                HEARTBEAT_IP: {
                  type: "string",
                  example: "169.254.0.1",
                },
                HEARTBEAT_SERVER_PORT: {
                  type: "string",
                  example: "502",
                },
                HEARTBEAT_ADDRESS: {
                  type: "string",
                  example: "0",
                },
                HEARTBEAT_PULSE: {
                  type: "string",
                  example: "2",
                },
                HEARTBEAT_MSG: {
                  type: "string",
                  example: "1",
                },
                _HEARTBEAT_USE: {
                  type: "string",
                  example: "true",
                },
                IMAGE_PATH: {
                  type: "string",
                  example: "/media/5BBF-40E8/Unsorted_Hose/",
                },
                _IMAGE_USE: {
                  type: "string",
                  example: "true",
                },
                PRED_BATCH_SIZE: {
                  type: "string",
                  example: "3",
                },
                READER_IP: {
                  type: "string",
                  example: "0.0.0.0",
                },
                READER_PORT: {
                  type: "string",
                  example: "1502",
                },
                READER_UPDATE: {
                  type: "string",
                  example: "5",
                },
                READER_ADDRESS: {
                  type: "string",
                  example: "0",
                },
                READER_LENGTH: {
                  type: "string",
                  example: "15",
                },
                READER_END_CHAR: {
                  type: "string",
                  example: "13",
                },
                _READER_USE: {
                  type: "string",
                  example: "true",
                },
                DEBUG_MODE: {
                  type: "string",
                  example: "true",
                },
              },
            },
            CameraSettingsFile: {
              required: [
                "AcquisitionFrameRate",
                "ExposureTime",
                "Gain",
                "Height",
                "LineMode",
                "LineSelector",
                "Width",
              ],
              type: "object",
              properties: {
                ExposureTime: {
                  type: "integer",
                  example: 900,
                },
                AcquisitionFrameRate: {
                  type: "integer",
                  example: 20,
                },
                Width: {
                  type: "integer",
                  example: 840,
                },
                Height: {
                  type: "integer",
                  example: 944,
                },
                Gain: {
                  type: "integer",
                  example: 63,
                },
                LineSelector: {
                  type: "integer",
                  example: 2,
                },
                LineMode: {
                  type: "integer",
                  example: 1,
                },
              },
            },
            DashboardSettingsFile: {
              required: [
                "DASHBOARD_IP",
                "DASHBOARD_PICTURE_SOURCE",
                "FINAL_PIC_PATH",
                "FINAL_SORTED_DIR",
                "NO_DEFECT_FOLDER",
                "UNSURE_DEFECT_FOLDER",
                "usb_mount_path",
              ],
              type: "object",
              properties: {
                usb_mount_path: {
                  type: "string",
                  example: "/media/",
                },
                DASHBOARD_PICTURE_SOURCE: {
                  type: "string",
                  example: "Unsorted_Hose",
                },
                FINAL_SORTED_DIR: {
                  type: "string",
                  example: "Hose_Pics",
                },
                NO_DEFECT_FOLDER: {
                  type: "string",
                  example: "No Defect",
                },
                UNSURE_DEFECT_FOLDER: {
                  type: "string",
                  example: "Not Sure",
                },
                FINAL_PIC_PATH: {
                  type: "string",
                  example: "/media/(USB_NAME)/Unsorted_Hose",
                },
                DASHBOARD_IP: {
                  type: "string",
                  example: "0.0.0.0:5000",
                },
              },
            },
          },
        },
      },
      // dom_id: "#swagger-ui",
    });
    // End Swagger UI call region
  },
  methods: {},
};
</script>
